import { requestQRCode, requestCalProfit } from '@/api/API'
import externalJs from '../../components/ExternalJs'
import BottomBar from '../../components/BottomBar'
import Header from '../../components/Header'

export default {
  name: 'generalize',
  components: {
    Header,
    BottomBar,
    externalJs
  },
  data () {
    return {
      gain_money: 0,
      qr_code: '/static/img/profile/default-user-img.png',
      host: window.location.host
    }
  },
  mounted () {
    this.getCalProfit()
    this.getQR()
  },
  methods: {
    async getCalProfit () {
      let user = this.$store.state.User || {}
      let res = await requestCalProfit(user.token)
      if (res.code === 200) {
        this.gain_money = res.data.cal_profit
      }
    },
    async getQR () {
      let user = this.$store.state.User || {}
      let res = await requestQRCode(user.token)
      if (res.code === 200) {
        this.qr_code = res.data.imgurl
      }
    },
    onShare (flag) {
      switch (flag) {
        case 1:
          console.log('qq')
          break
        case 2:
          console.log('qzone')
          break
        case 3:
          console.log('wechat')
          break
        case 4:
          console.log('weibo')
          break
        default:
          break
      }
    }
  }
}



